import React from "react";
import {Route, BrowserRouter as Router, Switch} from "react-router-dom";
import {Header, Footer} from "../components";
import {Home} from "../pages";

import "../scss/style.scss";

function App() {
  return (
    <div className="app">
      <Router>
        <Header />

        <Switch>
          <Route exact path="/" component={Home} />
        </Switch>

        <Footer />
      </Router>
    </div>
  );
}

export default App;
